@import url(./reset.css);

div.contact{
    position: relative;
    height: 100vh;
    padding: 100px;
    border: 20px solid #000;
    border-radius: 10px;
    box-sizing: border-box;
    section{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        h2{
            font-size: 50px;
            font-weight: bold;
            text-align: center;
            margin-bottom: 15px;
        }
        p{
            position: relative;
            text-align: center;
            font-size: 30px;
            margin-bottom: 30px;
            &::after{
                    content: "";
                    width: 100%;
                    height: 13px;
                    display: inline-block;
                    background: #ffe400;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    z-index: -1;
                    animation: ani 1s linear  ;
                }
                @keyframes ani {
                    0%{width: 0%;}
                    50%{width: 50%;}
                    100%{width: 100%;}
                }
        }
        ul{
            transform: translateX(15%);
            li{
                font-size: 20px;
                svg{
                    transform: translateY(25%);
                    margin-right: 10px;
                }
                span{
                    cursor: pointer;
                }
            }
        }
    }
}