@import url(./reset.css);

div.aboutme{
    position: relative;
    height: 100vh;
    padding: 100px;
    border: 20px solid #000;
    border-radius: 10px;
    box-sizing: border-box;
    section.profile{
        position: absolute;
        top: 15%;
        padding: 20px;
        //border: 1px solid red;
        h2{
            font-size: 50px;
            font-weight: bold;
            margin-bottom: 30px;
        }
        p{
            display: inline-block;
            position: relative;
            font-size: 20px;
            font-weight: bold;
            &::after{
                content: "";
                    width: 100%;
                    height: 10px;
                    display: inline-block;
                    background: #abf200;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    z-index: -1;
                    animation: ani 1s linear  ;
            }
            @keyframes ani {
                0%{width: 0%;}
                50%{width: 50%;}
                100%{width: 100%;}
            }

        }
        ul{
            margin-top: 15px;
            li{
                margin: 15px 10px;
                font-size: 18px;
                span{
                    font-weight: bold;
                }
            }
        }
    }
    section.skill{
        position: absolute;
        top: 75%;
        transform: translateY(-75%);
        padding: 20px;
        //border: 1px solid blue;
        h2{
            margin-bottom: 15px;
            font-size: 50px;
            font-weight: bold;
        }
        p{
            margin-bottom: 30px;
        }
        ul{
            display: inline-block;
            width: 80vw;
            &::after{
                display: block;
                content: "";
                clear: both;
            }
            li{
                float: left;
                font-size: 100px;
                margin:0 5%;
                &:nth-child(1){
                    color: orangered;
                }
                &:nth-child(2){
                    color: rgb(0, 110, 255);
                }
                &:nth-child(3){
                    color: rgb(23, 108, 204);
                }
                &:nth-child(4){
                    color: orange;
                }
                &:nth-child(5){
                    color: rgb(51, 231, 255);
                }
                
            }
        }
    }
}