@import url(./reset.css);

div.main{
    position: relative;
    border: 20px solid #000;
    border-radius: 10px;
    height: 100vh;
    box-sizing: border-box;
    section{
        position: absolute;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        top: 50%;
        width: 70vw;
        padding: 10%;
        p.hello{
            width: 300px;
            height: 50px;
            margin: 0 auto;
            font-size: 45px;
            font-weight: bold;
            text-align: center;
            overflow-y: hidden;
            span{
                position: relative;
                display: inline-block;
                width: 100%;
                height: 100%;
                animation: ShiftText 15s linear 1s infinite;
            }
            @keyframes ShiftText{
                0%{
                  opacity: 0;
                }
              
                1%,20%{
                  top: 0;
                  opacity: 1;
                }
                
                21%,40%{
                  top: -50px;
                }
              
                41%,60%{
                  top: -100px;
                }
              
                61%,80%{
                  top: -150px;
                }

                86%,100%{
                    top: -205px;
                }
              }
        }
        p{
            font-size: 25px;
            text-align: center;
            line-height: 50px;
            margin-bottom: 50px;
            span.hello{
                font-size: 50px;
                font-weight: bold;
            }
            span.name{
                position: relative;
                display: inline-block;
                font-size: 30px;
                font-weight: bold;
                &::after{
                    content: "";
                    width: 100%;
                    height: 15px;
                    display: inline-block;
                    background: #00d8ff;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    z-index: -1;
                    animation: ani 1s linear  ;
                }
                @keyframes ani {
                    0%{width: 0%;}
                    50%{width: 50%;}
                    100%{width: 100%;}
                }
            }
        }
        ul{
            display: inline-block;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            &::after{
                display: block;
                content: "";
                clear: both;
            }
            li{
                float: left;
                margin-right: 30px;
                font-size: 18px;
                a{
                    position: relative;
                    display: inline-block;
                    color: #000;
                    text-decoration: none;

                    &:after{
                        content: "";
                            width: 0;
                            height: 9px;
                            display: inline-block;
                            background: #00d8ff;
                            position: absolute;
                            bottom: 0;
                            left: 0;
                            z-index: -1;
                            transition: 0.5s ease-in ;
                    }
                }

                &:hover{
                    a{
                        font-weight: bold;
                        &::after{width: 100%;}
                    }
                }
            }
        }

    }
}