@import url(./reset.css);

div.project{
    position: relative;
    height: 100vh;
    padding: 50px;
    border: 20px solid #000;
    border-radius: 10px;
    box-sizing: border-box;
    section{
        position: absolute;
        left: 50%;
        top: 40%;
        transform: translateY(-50%) translateX(-50%);
        width: 100%;
        h2{
            font-size: 50px;
            font-weight: bold;
            text-align: center;
            margin-bottom: 25px;
        }
        p{
            margin-bottom: 50px;
            text-align: center;
        }
        ul{
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-template-rows: repeat(2, 1fr);
            text-align: center;

            li{
                padding: 20px;
                font-size: 15px;
                line-height: 35px;
                a{
                    color: #000;
                    text-decoration: none;
                    text-align: center;
                    span{
                        position: relative;
                        display: inline-block;
                        font-size: 28px;
                        font-weight: bold;
                        transition: 0.5s ease-in ;
                        // &::after{
                        //     content: "";
                        //     width: 0;
                        //     height: 14px;
                        //     display: inline-block;
                        //     background: #abf200;
                        //     position: absolute;
                        //     bottom: 0;
                        //     left: 0;
                        //     z-index: -1;
                        //     transition: 0.5s ease-in ;
                        // }
                    }
                }
                &:hover{                   
                    a{
                        span{
                            transform: scale(1.3);
                        // &::after{
                        //     width: 100%;
                        //     }
                        }
                    }      
                 }
                &:nth-child(6){
                    a{
                        img{
                            width: 70px;
                        }
                    }
                }
                
            }
                
            
        }
    }
}