@import url(./reset.css);

div.nav{
    position: absolute;
    bottom: 15px;
    left: 50%;
    transform: translateX(-50%);
    padding: 20px;
    background: rgba(0,0,0,0.05);
    border-radius: 45px;
    ul{
        &::after{
            display: block;
            content: "";
            clear: both;
        }
        a{
            float: left;
            color: #000;
            text-decoration: none;
            margin-right: 25px;
            &:nth-of-type(4){
                margin-right: 0;
            }
            li{
                position: relative;
                width: 100px;
                height: 100px;
                font-size: 22px;
                font-weight: bold;
                text-align: center;
                background: #005bea;
                border-radius: 20px;
                color: #fff;
                cursor: pointer;
                box-sizing: border-box;
                transition: all ease 0.5s 0s;
                span{
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translateX(-50%) translateY(-50%);
                }
                &:hover{
                    animation: hoverani 0.3s linear infinite alternate;
                    @keyframes hoverani {
                        0%{transform: rotate(2.5deg) translateY(1.5px) }
                        25%{transform: rotate(0deg) translateX(1.5px);}
                        50%{transform: rotate(-2.5deg) translateY(0) }
                        75%{transform: rotate(0deg)  translateX(0);}
                        100%{transform: rotate(2.5deg) translateY(-1.5px);}
                    }
                }
                
            }
        }
        
    }
}