@import url(./reset.css);

div.samsung{
    position: relative;
    height: 100vh;
    padding: 100px;
    border: 20px solid #000;
    border-radius: 10px;
    box-sizing: border-box;
    button.prev{
        position: absolute;
        top: 50px; left: 50px;
        width: 30px; height: 30px;
        border-radius: 50%;
        border: none;
        background: #dfe9f3;
        cursor: pointer;
        transition: all ease 1s 0s;
        svg{
            position: absolute;
            top: 50%; left: 50%;
            transform: translateY(-50%) translateX(-50%);
        }
        &:hover{
            background: #000;
            svg{
                color: #dfe9f3;
            }
        }
    }
    section{
        position: relative;
        h2{
            font-size: 50px;
            font-weight: bold;
            margin-bottom: 30px;
        }
        p.site{
            display: inline-block;
            padding: 10px;
            border: 1.5px solid #dfe9f3;
            font-size: 18px;
            cursor: pointer;
            transition: all ease 1s 0s;
            &:hover{
                font-weight: bold;
                background: #000;
                color: #dfe9f3;
            }
        }
        ul.explain{
            position: absolute;
            top: 0;
            right: 0;
            li{
                font-size: 20px;
                margin-bottom: 15px;
                ul{
                    margin-top: 10px;
                    li{
                        float: left;
                        margin-right:30px;
                        font-size: 55px;
                    }
                }
            }
            li.lang{
                font-weight: bold;
            }
            
        }
        div{
            width: 85%;
            margin-top: 70px;
            padding: 20px;
            border: 1px solid #dfe9f3;
            background-image: linear-gradient(to bottom, #dfe9f3 0%, white 100%);
            box-sizing: border-box;
            h3{
                font-size: 30px;
                font-weight: bold;
            }
            img{
                width: 55%;
                transform: translateX(40%);
            }
        }
        ul.buttons{
            position: absolute;
            right: 0;
            top: 50%;
            li{
                margin-bottom: 30%;
                button{
                    padding: 10px;
                    border: 1px solid #dfe9f3;
                    background: none;
                    font-size: 18px;
                    cursor: pointer;
                    transition: all ease 1s 0s;
                    &:hover{
                        font-weight: bold;
                        background: #000;
                        color: #dfe9f3;
                    }
                }
            }
        }
    }
}